/*
 ** 处理顶部导航的action
 ** @param state{object} 状态
 ** @param action(string) action事件
 */
import {
	fromJS,
} from 'immutable'
import {
	HEADLINE_ONCLICK,
	GET_HEADLINE,
	HEADLINE_SLIDE,
	GET_LAST_LIST,
	GET_HOTLIST,
	GET_ONCLICK_LIST,
	UPLOAD_PIC_WALL,
	GET_PIC_WALL
} from './action'
import {
	getHeadline
} from './../../modules/datas';
import cloneDeep from 'lodash/cloneDeep'
import options from './../../modules/option'
const domain = 'http://www.subui.com';

const checkImg = (imgurl) => {
	if (!imgurl) {
		return false
	}
	return (new RegExp(domain)).test(imgurl) ? imgurl : `${domain}${imgurl}`
}
const checkSrc = (item) => {
		if (options.staticUrl) {
			return (new RegExp(domain)).test(item.titleurl) ? item.titleurl : `${domain}${item.titleurl}`
		} else {
			return `/${item.table}/${item.classid}/${item.id}`;
		}
	}
	// 处理幻灯图需要的数据
const renderSlide = (data) => {
	if (!data || !data.length) {
		return data;
	}
	const bond = {
		result: [],
		width: 640,
		interval: 4000,
		height: 320,
		currentIndex: 0,
		slideLength: 0,
	};
	data.map(function(item, index){
		if (item.titlepic) {
			bond.result.push({
				src: checkSrc(item),
				imgUrl: checkImg(item.titlepic),
				title: item.title,
				active: index === 0 ? true : false,
			});
			bond.slideLength++;
		}
		return item;
	})
	return fromJS(bond);
};

const initialState = fromJS({
	news: [],
	headline: renderSlide(getHeadline.result[0]),
	newslist: [],
	hotlist:[],
	onclicklist:[],
	picwall:[],
});

function reducer(state = initialState, action) {
	switch (action.type) {
		case GET_HEADLINE + '_SUCCESS':
			// 幻灯图抓取成功
			const result = action.payload.result && action.payload.result.length ? action.payload.result[0] : [];
			return state.set('headline', renderSlide(result));
		case GET_LAST_LIST + '_SUCCESS':
			let newsResult = action.payload.result[0],
				newslist = [];
			if (!newsResult.length) {
				return state
			};
			newsResult.map((item, index) => {
				newslist.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
				})
			})
			return state.set('newslist', newslist);
		case GET_HOTLIST + '_SUCCESS':
		// 今日要闻
			let hotResult = action.payload.result[0],
				hotlist = [];
			if (!hotResult.length) {
				return state
			};
			hotResult.map((item, index) => {
				hotlist.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
					desc:item.smalltext
				})
			})
			return state.set('hotlist', hotlist);
		case GET_PIC_WALL + '_SUCCESS':
		// 图片墙
			let picResult = action.payload.result[0],
				picwall = [];
			if (!picResult.length) {
				return state
			};
			picResult.map((item, index) => {
				picwall.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
					desc:item.smalltext,
					styles:{
					},
				})
			})
			return state.set('picwall', picwall);
		case GET_ONCLICK_LIST + '_SUCCESS':
		// 热门点击
			let onclickResult = action.payload.result[0],
				onclickList = [];
			if (!onclickResult.length) {
				return state
			};
			onclickResult.map((item, index) => {
				onclickList.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
					desc:item.smalltext,
					sort:true
				})
			})
			return state.set('onclicklist', onclickList);
		case HEADLINE_ONCLICK:
			// 幻灯图点击切换
			// var newS = cloneDeep(state.toJS().headline);
			// newS.currentIndex = +action.payload.index;
			// return state.set('headline', newS);
			return state.setIn(['headline','currentIndex'],+action.payload.index)
		case GET_HEADLINE + '_FAILURE':
			return state;
		case HEADLINE_SLIDE:
			// 处理幻灯图的自动播放
			var newS = state.toJS().headline,
				imgCount = newS.slideLength,
				curIndex = newS.currentIndex;
			let nextIndex = (curIndex + 1) >= imgCount ? 0 : curIndex + 1;
			// newS.currentIndex = nextIndex;
			return state.setIn(['headline','currentIndex'], nextIndex);
		case UPLOAD_PIC_WALL:
			// 更新图片墙
			let newPicWall = cloneDeep(state.toJS().picwall);
			newPicWall[+action.payload.index]['styles'] = action.payload.styles;
		return state.set('picwall',newPicWall);
		default:
			return state;
	}
}
export default reducer;


